//// variables

//	colors
$gn01:	#007F41;
$gn02:	#E6F5BD;
$gn03:	#003C18;
$bl01:	#260096;
$pk01:	#FCE2E2;
$pk02:	#FFF5FA;
$yl01:	#FFFAB8;
$yl02:	#FFF0BE;
$sk01:	#D9F8FF;
$rd01:	#F84263;
$or01:	#FB6736;
$gy01:	#4B4E53;

// unit
$gap: 20px;
$sp: 767px;

// font
$fontXS: 12px;
$fontS:	14px;
$fontM:	16px;
$fontL:	20px;
$fontXL: 24px;


// mixin

@mixin tr {
	transition: all .5s ease;
}

@mixin note {
	font-weight: normal;
	line-height: 1.5;
	font-size: $fontXS;
	@media (min-width: $sp + 1) {
		font-size: $fontS;
		letter-spacing: 0.05rem;
	}
}

@mixin block1 {
	margin-top: $gap * 2;
}

@mixin flex {
	display: flex;
}

@mixin flex_sp_ini {
	@media (max-width: $sp) {
		flex-wrap: wrap;
		.div_l, .div_c, .div_r {
			flex-basis: 100%;
		}
	}
}

#contents_in {
	@media (max-width: $sp) {
		padding: 0;
	}
}

.content_inner {
	color: #000;
	line-height: 1.75;
	font-size: 16px;
	font-weight: 600;
	text-align: justify;
	@media (max-width: $sp) {
		box-shadow: none;
		padding: 0;
	}

	//// common
	.note {
		@include note;
	}
	.btn_apply {
		text-align: center;
		@include block1;
		@media (max-width: $sp) {
			margin-top: $gap;
		}
		a {
			@include tr;
			&:hover {
				opacity: .85;	
			}
		}
	}
	.flex {
		@include flex;
	}
	
	.sp_v {
		@media (min-width: $sp+1) {
			display: none;
		}
	}
	.pc_v {
		@media (max-width: $sp) {
			display: none;
		}
	}
	
	//// individual
	.header_main {
		background: $gn01;
		@media (min-width: $sp + 1) {
			margin: $gap * -1 $gap * -1 0;
			padding: $gap / 4  $gap / 2 0 0;
		}
		h1 {
			@include flex;
			.badge {
				width: 220px;
			}
			.title {
				width: 1020px;
				text-align: center;
				img {
					&:last-child {
						border-top: 1px solid #fff;
					}
				}
			}
		}
	}
	section {
		margin: $gap $gap / 2 0;
		@media (min-width: $sp + 1) {
			margin: $gap * 2 auto 0;
		}
		&.sec_apply {
			> .btn_apply {
				margin-top: 0;
			}
		}
		&.sec01 {
			position: relative;
			@media (min-width: $sp + 1) {
				margin: 0 $gap * -1 0;
			}
			@media (max-width: $sp) {
				background: linear-gradient(#FABBDD, #fff);
				margin: 0;
			}
			.badge {
				position: absolute;
				top: 0;
				left: 0;
				@media (max-width: $sp) {
					width: 30vw;
					max-width: 220px;
				}
			}
			.div_r {
				@media (max-width: $sp) {
					margin-top: $gap;
					padding: 0 $gap / 2;
				}
				@media (min-width: $sp + 1) {
					position: absolute;
					top: $gap * 2;
					right: $gap * 2;
					width: 402px;
				}
				.youtube {
					background: $bl01;
					padding: $gap / 2;
					iframe {
						width: 382px;
						height: 215px;
						margin-top: $gap / 2;
					}
					@media (max-width: $sp) {
						.ifr_cnt {
							position: relative;
							width: 100%;
							padding: 56.25% 0 0 0;
							margin-bottom: $gap / 2;
							iframe {
								position: absolute;
								top: 0;
								left: 0;
								width: 100%;
								height: 100%;
							}
						}
					}
					+ p {
						text-align: right;
						margin-top: $gap * 2 ;
						img {
							&:first-child {
								margin-right: $gap / 1.5;
							}
							&:last-child {
								margin-top: $gap / 4;
							}
						}
					}
					div {
						margin-top: $gap / 2;
					}
				}
			}
			.dacky {
				@media (max-width: $sp) {
					> picture {
						display: block;
						transform: scale(1.1) translate(6vw , $gap * 1.1);
					}
				}
				@media (min-width: $sp + 1) {
					position: relative;
					top: $gap * -.5;
				}
				.txts {
					margin-top: $gap * -3;
					position: relative;
					z-index: 1;
					@media (max-width: $sp) {
						padding: 0 $gap / 2;
					}
					@media (min-width: $sp + 1) {
						position: absolute;
					    left: 204px;
					    bottom: 60px;
						width: 648px;
					}
				}
			}
			.btn_apply {
				margin-top: $gap;
				@media (min-width: $sp + 1) {
					margin-top: $gap * -2;
					position: relative;
					z-index: 2;
				}
			}
		} // .sec01
		&.sec02 {
			background: $pk01;
			border-bottom-left-radius: $gap / 2;
			border-bottom-right-radius: $gap / 2;
			@media (max-width: $sp) {
				padding-bottom: $gap / 3;
			}
			h2 {
				img {
					@media (min-width: $sp + 1) {
						max-width: none;
						height: 103px;
					}
				}
				+ div {
					margin: $gap;
					@media (min-width: $sp + 1) {
						margin: $gap $gap * 2 0;
					}
				}
			}
			.flex {
				justify-content: space-between;
				@media (max-width: $sp) {
					margin-top: $gap / 2;
				}
				.div_l {
					font-weight: bold;
					font-size: $fontL;
					line-height: 200%;
					flex-basis: 60%;
					.note {
						margin-top: $gap;
						font-size: $fontS;
						font-weight: normal;
					}
					@media (max-width: $sp) {
						order: 2;
						flex-basis: 100%;
						font-size: $fontM;
						margin-top: $gap / 2;
					}
				}
				.div_r {
					position: relative;
					top: $gap * -.75;
					text-align: center;
					flex-basis: 35%;
					@media (max-width: $sp) {
						order: 1;
						flex-basis: 100%;
						top: 0;
						picture {
							&:last-child {
								margin-top: $gap;
								display: block;
							}
						}
					}
				}
				@include flex_sp_ini;
			}
		} // .sec02
		&.sec03 {
			background: #F5FFA8;
			border:	solid 8px #558E3D;
			border-radius: $gap / 2;
			margin-top: $gap * 3;
			@media (min-width: $sp + 1) {
				margin-top: $gap * 4;
			}
			h2 {
			    margin: $gap * -2 0 0 0;
			    transform: scale(1.1);			
				@media (min-width: $sp + 1) {
				    margin: $gap * -3 0 0 $gap *-.75;				
				    width: 1245px;
				}
			}
			.flex {
				margin: 0 $gap / 2 $gap / 2;
				@media (min-width: $sp + 1) {
					margin: $gap * -1 $gap $gap;
				}
				justify-content: space-between;
				.div_l {
					flex-basis: 40%;
					@media (max-width: $sp) {
						order: 2;
					}
				}
				.div_r {
					flex-basis: 60%;
					font-size: $fontL;
					padding: $gap / 2;
					@media (min-width: $sp + 1) {
						font-size: $fontXL;
						padding: $gap;
					}
					@media (max-width: $sp) {
						order: 1;
					}
					.note {
						margin-top: $gap;
					}
				}
				@include flex_sp_ini;
			}
		} // .sec03
		&.sec04 {
			background: $yl01;
			border: solid 4px $pk01;
			border-bottom-left-radius: $gap / 2;
			border-bottom-right-radius: $gap / 2;
			h2 {
				background: #D81718;
				padding: $gap / 2 $gap / 4;
				margin: -4px 0 0 -4px;
				width: calc(100% + 8px);
				@media (min-width: $sp + 1) {
					height: 92px;
					padding: $gap / 2 $gap * 2 $gap  / 2;
				}
				@include flex;
				justify-content: space-between;
				@media (max-width: $sp) {
					flex-wrap: wrap;
					img {
						flex-basis: 100%;
						display: block;
						&:last-child {
							margin-top: $gap / 4;
						}
					}
				}
			}
			.flex {
				overflow: hidden;
				border-bottom-left-radius: $gap / 4;
				border-bottom-right-radius: $gap / 4;
				.div_l {
					flex-basis: 25%;
					@media (max-width: $sp) {
						img {
							display: block;
							margin: auto;
							width: 70%;
							max-width: 400px;
							transform: translateY($gap / 3);
						}
					}
				}
				.div_c {
					flex-basis: 43%;
					h3 {
						+ p {
							margin-top: $gap * -1;
							font-size: $fontL;
							padding: 0 $gap / 2;
							@media (max-width: $sp) {
								margin-top: $gap / -4;
							font-size: $fontM;
							}					
						}
					}
					.note {
						margin-top: $gap / 2;
						@media (max-width: $sp) {
							padding: 0 $gap / 2;
						}
					}
				}
				.div_r {
					flex-basis: 34%;
					@media (max-width: $sp) {
						margin-top: $gap / 2;
					}
				}
				@include flex_sp_ini;
			}
		} //.sec04
		&.sec05 {
			background: $yl02;
			box-shadow:	0px 0px $gap $gap / 2 $yl02;
			border-radius: $gap / 2;
			width: calc(100% - #{$gap * 2});
			margin-top: $gap * 1.5;
			margin-left: auto;
			margin-right: auto;
			@media (min-width: $sp + 1) {
				margin-top: $gap * 3;
				padding: $gap / 2;
			}
			h2 {
				text-align: center;
				img {
					width: 1120px;
				}
			}
			.flex {
				&:first-of-type {
					justify-content: space-between;
					margin-top: $gap;
					.card {
						background: #fff;
						border-radius: $gap / 2;
						overflow: hidden;
						margin-top: $gap / 2;
						&.div_l {
							margin-top: 0 !important;
						}
						&.div_c {
							@media (max-width: $sp) {
								div {
									div {
										img {
											width: 60%;
											max-width: 280px;
										}
									}
								}
							}
						}
						&.div_r {
							@media (max-width: $sp) {
								div {
									div {
										img {
											width: 60%;
											max-width: 280px;
										}
									}
								}
							}
						}
						@media (min-width: $sp + 1) {
							margin-top: 0;
							flex-basis: calc( 33.3% - #{$gap} );
						}
						h3 {
							padding: $gap / 2;
							background: #D9F8FF;
							text-align: center;
							@media (max-width: $sp) {
								padding: $gap / 2 $gap * 3;
							}
							+ div {
								padding: $gap;
								p {
									text-align: justify;
									font-size: $fontM;
									@media (min-width: $sp + 1) {
										font-size: $fontL;
									}
									&:first-child {
										text-align: center;								
										+ * {
											margin-top: $gap;
										}
									}
								}
								div {
									text-align: center;
									@media (min-width: $sp + 1) {
										margin-top: $gap * 2;
									}
								}
							}
						}
						&:first-child {
							h3 {
								+ div {
									div {
										@media (min-width: $sp + 1) {
											margin-top: $gap * 4;
										}
									}
								}
							}
						}
						&:last-child {
							@media (min-width: $sp + 1) {
								margin-top: 0;
							}
							h3 {
								+ div {
									p {
										&:first-child {
											+ * {
												margin-top: 0;
											}
										}
									}
									div {
										margin-top: 0;
									}
								}
							}
						}
					}
					@include flex_sp_ini;
				}
				&:last-of-type {
					@media (min-width: $sp + 1) {
						margin-top: $gap * 2;
					}
					h2 {
						padding: $gap / 2 $gap / 1.5 0;
					}
					> .div_l {
						flex-basis: 50%;
						overflow: hidden;
						> img {
							transform: scale(1.2);
						}
						dl {
							margin-top: $gap * -1;
							@media (min-width: $sp + 1) {
								margin-top: 0;
								width: calc(100% - #{$gap});
							}
							dt {
								text-align: center;
							}
							dd {
								padding: $gap / 2 $gap 0;
								ul {
									text-shadow: 2px 2px 0px #fff;
									@media (min-width: $sp + 1) {
										font-size: 23px;
									}
									li {
										border-top: 1px solid #FB6736;
										&:first-child {
											border-top: none;
										}
										&:nth-child(odd) {
											&:before {
												content: "●";
												color: $or01;
											}
										}
										&:nth-child(even) {
											&:before {
												content: "★";
												color: $gn01;
											}
										}
									}
								}
							}
						}
					}
					> .div_r {
						background: $rd01;
						border-radius: $gap / 2;
						flex-basis: 50%;
						@media (max-width: $sp) {
							margin-top: $gap / 2;
						}
						.inner {
							background: #fff;
							margin: $gap /  2;
							padding: $gap;
							@media (min-width: $sp + 1) {
								font-size: $fontL;
								line-height: 1.35;
							}
							.flex {
								margin-top: $gap / 4;
								@include flex_sp_ini;
							}
							> div {
								p {
									@media (min-width: $sp + 1) {
										flex-basis: 50%;
									}
									&:last-child {
										margin-top: $gap / 2;
									}
								}
								div {
									@media (min-width: $sp + 1) {
										flex-basis: 50%;
									}
								}
								&:nth-of-type(2) {
									border-top: dotted 4px $rd01;
									padding-top: $gap / 2;
									margin-top: $gap / 2;
									> div {
										div {
											margin-top: $gap / 2;
											@media (min-width: $sp + 1) {
												margin-top: $gap * -4;
											}
										}
									}
								}
								&:nth-of-type(3) {
									margin-top: $gap / 2;
									@media (min-width: $sp + 1) {
										margin-top: $gap * -2;
									}
									> p {
										text-align: right;
										@media (max-width: $sp) {
											width: 220px;
											text-align: center;
											margin: 0 auto;
										}
									}
									> div {
										div {
											@media (min-width: $sp + 1) {
												margin-top: $gap * -3;
											}
										}
										@media (max-width: $sp) {
											div {
												order: 2;
												margin-top: $gap / 2;
											}
											p {
												order: 1;
												margin-top: 0;
											}
										}
									}
								}
							}
						}
					}
					@include flex_sp_ini;
				}
			}
		} // .sec05
		&.sec06 {
			background: $pk02;
			border: $rd01 solid 6px;
			border-radius: $gap;
			padding: $gap / 2;
			position: relative;
			border-radius: $gap / 2;
			@media (min-width: $sp + 1) {
				margin-top: $gap * 5;
				padding: $gap * 1.5;
				border-radius: $gap;
				&:after {
					content: "";
					position: absolute;
					top: $gap / 2;
					left: $gap / 2;
					width: calc( 100% - #{$gap * 1.5});
					height: calc( 100% - #{$gap * 1.5});
					border: $rd01 dotted 6px;
					border-radius: $gap / 2;
				}
			}
			> h2 {
				text-align: center;
				position: relative;
				z-index: 2;
				@media (min-width: $sp + 1) {
					margin-top: $gap * -5;
				}
			}
			> .flex {
				border-top: 2px solid $rd01;
				padding-top: $gap * 1.5;
				justify-content: space-between;
				margin: $gap / 2;
				&:first-of-type {
					border-top: none;
					padding-top: 0;
				}
				&:last-of-type {
					margin-bottom: 0;
				}
				@media (min-width: $sp + 1) {
					margin: $gap;
				}
				.div_l {
					flex-basis: 50%;
					h2 {
						img {
							display: block;
							&:first-child {
								@media (max-width: $sp) {
									height: 38px;
									display: block;
									margin: auto;
								}					
							}
							&:last-child {
								margin-top: $gap / 2;
							}
						}
						+ p {
							margin-top: $gap;
							@media (min-width: $sp + 1) {
								font-size: 18px;
							}
						}
					}
				}
				.div_r {
					flex-basis: 50%;
					text-align: center;
				}
				&:nth-of-type(odd) {
					.div_r {
						text-align: center;
					}
				}
				&:nth-of-type(even) {
					.div_l {
						order: 2;
					}
					.div_r {
						order: 1;
					}
				}
				&:nth-of-type(1) {
					.div_l {
					}
					.div_r {
						img {
							@media (min-width: $sp + 1) {
								position: relative;
								top: $gap * -1.5;
							}
						}
					}
				}
				&:nth-of-type(3) {
					@media (min-width: $sp + 1) {
						.div_l {
							flex-basis: 40%;
						}
						.div_r {
							flex-basis: 60%;
						}
					}
				}
				&:nth-of-type(4) {
					.div_r {
						@media (min-width: $sp + 1) {
							padding-right: $gap;
						}
					}
				}
				&:nth-of-type(even) {
					.div_r {
						@media (max-width: $sp) {
							order: 2;
						}
					}
					.div_l {
						@media (max-width: $sp) {
							order: 1;
						}
					}
				}
				@include flex_sp_ini;
			}
		} // .sec06
		&.sec07 {
			border-radius: $gap / 2;
			overflow: hidden;
			header {
				text-align: center;
				font-size: $fontXL;
				padding: $gap;
				background: $gn02 url(/contents/shop/00000003/image/static/dacky/dc_img121@2x.png) no-repeat 100% 100%;
				background-size: 20vw;
				@media (min-width: $sp + 1) {
					background-position: 100% 50%;
					background-size: 220px;
				}
				h1 {
					img {
						display: block;
						margin: auto;
						&:last-child {
							margin-top: $gap  / 2;
						}
					}
					+ p {
						line-height: 1.5;
						margin-top: $gap * -.5;
						@media (max-width: $sp) {
							margin-top: $gap / 2;
							font-size: $fontL;
							min-height: 7rem;
							padding-right: 16vw;
							br {
								display: none;
							}
						}
						@media (max-width: 500px) {
							font-size: $fontS;
							min-height: auto;
						}
					}
				}
			}
			.flex {
				> div {
					flex-basis: 47%;
					padding: $gap / 2;
					&.div_l {
						@media (min-width: $sp + 1) {
							padding: $gap / 4;
							display: flex;
							align-items: center;
							justify-content: center;
							flex-basis: 6%;
						}
						p {
							color: #fff;
							font-size: $fontXL;
							text-align: center;
							line-height: 1;
							@media (min-width: $sp + 1) {
								font-size: 48px;
							}
						}
					}
				}
				&:nth-of-type(1) {
					> div {
						background: #e2f5fa;
						&.div_l {
							background: #6FD9F8;
						}
					}
				}
				&:nth-of-type(2) {
					> div {
						background: #ffede8;
						&.div_l {
							background: #FF9478;
						}
						&.div_r {
							img {
								margin-top: $gap * -1;
							}
						}
					}
				}
				&:nth-of-type(3) {
					> div {
						background: #eaedfd;
						&.div_l {
							background: #4252AB;
						}
						&.div_c {
							img {
								margin-top: $gap * -.75;
							}
						}
					}
				}
				@include flex_sp_ini;
			}
		} // .sec07
		&.sec08 {
			background: linear-gradient(#FFC37E, #ffedd8);
			border-radius: $gap / 2;
			overflow: hidden;
			position: relative;
			padding: $gap * 1.25 $gap * 1.25 $gap * 1.5;
			@media (min-width: $sp + 1) {
				padding: $gap * 1.5 $gap * 1.5 $gap * 2;
			}
			&:after {
				content: "";
				position: absolute;
				top: $gap / 3.5;
				left: $gap / 3.5;
				width: calc( 100% - #{$gap * 1});
				height: calc( 100% - #{$gap * 1});
				border: #fff solid 4px;
				border-radius: $gap / 4;
			}
			.flex {
					justify-content: space-between;
					position: relative;
					z-index: 1;
				> ul {
					flex-basis: calc(50% - #{$gap / 2});
					margin-top: $gap * -.5;
					@media (min-width: $sp + 1) {
						margin-top: $gap * -1.5;
					}
					li {
						background: #F0FFFE;
						border-radius: $gap / 2;
						box-shadow: 4px 4px 4px rgba(0, 0, 0, .25);
						padding: $gap / 1.5;
						margin-top: $gap / 2;
						&:first-child {
							margin-top: 0;
						}
						@media (min-width: $sp + 1) {
							padding: $gap;
						}
					}
					&:last-child {
						li {
							&:last-child {
								background: none;
								box-shadow: none;
								padding: $gap * .75 0 0;
							}
						}
					}
				}
				@include flex_sp_ini;
			}
		} // .sec08
		&.sec09 {
			border: 3px solid $gy01;
			border-radius: $gap;
			margin-top: $gap * 2;
			@media (min-width: $sp + 1) {
				margin-top: $gap * 4;
			}
			h2 {
				background: $gy01;
				padding: $gap  / 2;
				border-top-left-radius: $gap * .75;
				border-top-right-radius: $gap * .75;
				line-height: 1;
				img {
					&:first-child {
						margin-top: $gap * -2;
						@media (min-width: $sp + 1) {
							margin-top: $gap * -3;
						}
					}
				}
			}
			> .flex {
				padding: $gap;
				@media (min-width: $sp + 1) {
					padding: $gap * 1.5;
				}
				> p {
					line-height: 1.5;
					@media (min-width: $sp + 1) {
						font-size: $fontXL;
					}
				}
				> .div_l {
					@media (min-width: $sp + 1) {
						margin-right: $gap * 2;
						flex-basis: 60%;
					}
				}
				> .div_r {
					flex-basis: 40%;
					@media (max-width: $sp) {
						margin-top: $gap;
						flex-basis: auto;
					}
					&.flex {
						justify-content: space-between;
						.div_l {
							flex-basis: 196px;
							img {
								@media (max-width: $sp) {
									width: 60%;
									max-width: 300px;
									display: block;
									margin: auto;
								}
							}
						}
						.div_r {
							flex-basis: calc(100% - (196px + #{$gap}));
							
							font-size: $fontM;
							line-height: 1.5;
							font-weight: normal;
							P {
								&:first-child {
									text-align: left;
									@media (max-width: $sp) {
										margin-top: $gap / 2;
									}
									img {
										display: block;
										margin: $gap / 2 auto auto;
										@media (max-width: $sp) {
											width: 60%;
											max-width: 300px;
											display: block;
											margin: $gap / 4 auto auto;
										}
									}
								}
							}
							.prof {
								margin-top: $gap / 2;
								padding-top: $gap / 2;
								border-top: 1px solid #000;
							}
						}
						@include flex_sp_ini;
					}
				}
				@include flex_sp_ini;
			}
		} // .sec09
		&.sec10 {
			border: 4px solid $gn03;
			h2 {
				background: $bl01;
				text-align: center;
				padding: $gap / 2;
				@media (min-width: $sp+1 ) {
					padding: $gap;
				}
				img {
					width: 100%;
				}
				+ * {
					border-top: 4px solid $gn03;
					background: $yl01;
					@media (min-width: $sp + 1 ) {
						padding: $gap;
					}
					&.flex {
						.div_l {
							flex-basis: calc( 100% - 418px );
							padding: $gap;
							@media (min-width: $sp + 1 ) {
								padding-right: $gap;
							}
							dt {
								background: $bl01;
								font-size: $fontL;
								font-weight: normal;
								text-align: center;
								color: #fff;
								border-radius: 2rem;
								padding: $gap / 2;
								line-height: 1rem;
								
							}
							dd {
								margin-top: $gap / 2;
								padding: $gap  / 2 0 0 0;
								border-top: 1px solid $bl01;
								position: relative;
								font-size: $fontM;
								.title {
									color: $bl01;
									font-family: serif;
									font-size: $fontL;
									&:before {
										content: "◉";
										color: $bl01;
										font-size: $fontL;
										margin-right: 0;
										padding: 0;
										border-radius: 0;
									}
									@media (min-width: $sp + 1 ) {
										font-size: $fontXL;
									}
								}
								.img {
									width: 60%;
									max-width: 300px;
									display: block;
									margin: $gap / 2 auto;
									@media (min-width: $sp + 1 ) {
										width: auto;
										max-width: 100%;
										position: absolute;
										right: 0;
										top: $gap;
										z-index: 2;
									}
								}
								&:nth-of-type(1) {
									border-top: none;
									margin-top: 0;
								}
								&:nth-of-type(2) {
									@media (min-width: $sp + 1 ) {
										padding-right: 30%;
										.img {
											top: auto;
											bottom: $gap / 4;
										}
									}
								}
								&:nth-of-type(3) {
									@media (min-width: $sp + 1 ) {
										padding-right: 30%;
									}
								}
							}
							@media (max-width: $sp) {
								order: 2;
							}
						}
						.div_r {
							flex-basis: calc( 418px + #{$gap} );
							text-align: right;
							@media (max-width: $sp) {
								order: 1;
							}
						}
						@include flex_sp_ini;
					}
				}
			}
		} // .sec10
		&.sec11 {
			// duplication of .sec4;
		}
		&.sec12 {
			margin-top: $gap * 1.25;
			.flex {
				justify-content: center;
				.div_r {
					margin: $gap 0 0;
					@media (min-width: $sp + 1 ) {
						margin: 22px 0 0 $gap;
					}
					> * {
						margin-top: $gap * .8;
						display: block;
						&:first-child {
							margin-top: 0;
						}
					}
				}
				@include flex_sp_ini;
			}
		} // .sec12
		&.sec13 {
			border: 2px solid #000;
			font-weight: normal;
			h2 {
				background: #F9F2DE;
				font-size: $fontL;
				color: #CC2F2A;
				text-align: center;
				padding: $gap  / 4;
				@media (min-width: $sp + 1 ) {
					font-size: $fontXL * 1.25;
				}
				+ div {
					padding: $gap;
					@media (min-width: $sp + 1 ) {
						padding: $gap $gap * 2;
					}
					p {
						font-size: $fontS;
						margin-top: $gap;
						&:first-child {
							margin-top: 0;
						}
					}
				}
			}
		} // .sec13
	}
	.bd_box01 {
		border: 1px solid #000;
		border-radius: $gap / 4;
		padding: $gap / 2;
		align-items: center;
		margin: $gap auto 0;
		@media (min-width: $sp + 1 ) {
			margin: $gap * 2 auto 0;
			width: 908px;
		}
		.div_l {
			width: 40%;
			text-align: center;
			.note {
				text-align: right;
			}
		}
		.bold {
			font-size: $fontXL * 1.5;
			font-weight: bold;
			vertical-align: baseline;
		}
		p {
			&:first-of-type {
				width: 100%;
				border-bottom: 1px solid #000;
				padding-bottom: $gap / 8;
				margin-bottom: $gap / 2;
			}
		}
		@include flex_sp_ini;
	}
}


/* Magnific Popup CSS */

.mfp-bg {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1042;
	overflow: hidden;
	position: fixed;
	background: #0b0b0b;
	opacity: 0.8;
}

.mfp-wrap {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1043;
	position: fixed;
	outline: none !important;
	-webkit-backface-visibility: hidden;
}

.mfp-container {
	text-align: center;
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	padding: 0 8px;
	box-sizing: border-box;
	&:before {
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
	}
}

.mfp-align-top .mfp-container:before {
	display: none;
}

.mfp-content {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin: 0 auto;
	text-align: left;
	z-index: 1045;
}

.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
	width: 100%;
	cursor: auto;
}

.mfp-ajax-cur {
	cursor: progress;
}

.mfp-zoom-out-cur {
	cursor: -moz-zoom-out;
	cursor: -webkit-zoom-out;
	cursor: zoom-out;
	.mfp-image-holder .mfp-close {
		cursor: -moz-zoom-out;
		cursor: -webkit-zoom-out;
		cursor: zoom-out;
	}
}

.mfp-zoom {
	cursor: pointer;
	cursor: -webkit-zoom-in;
	cursor: -moz-zoom-in;
	cursor: zoom-in;
}

.mfp-auto-cursor .mfp-content {
	cursor: auto;
}

.mfp-close,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;
}

.mfp-loading.mfp-figure {
	display: none;
}

.mfp-hide {
	display: none !important;
}

.mfp-preloader {
	color: #CCC;
	position: absolute;
	top: 50%;
	width: auto;
	text-align: center;
	margin-top: -0.8em;
	left: 8px;
	right: 8px;
	z-index: 1044;
	a {
		color: #CCC;
		&:hover {
			color: #FFF;
		}
	}
}

.mfp-s-ready .mfp-preloader,
.mfp-s-error .mfp-content {
	display: none;
}

button {
	&.mfp-close,
	&.mfp-arrow {
		overflow: visible;
		cursor: pointer;
		background: transparent;
		border: 0;
		-webkit-appearance: none;
		display: block;
		outline: none;
		padding: 0;
		z-index: 1046;
		box-shadow: none;
		touch-action: manipulation;
	}
	&::-moz-focus-inner {
		padding: 0;
		border: 0;
	}
}

.mfp-close {
	width: 44px;
	height: 44px;
	line-height: 44px;
	position: absolute;
	right: 0;
	top: 0;
	text-decoration: none;
	text-align: center;
	opacity: 0.65;
	padding: 0 0 18px 10px;
	color: #FFF;
	font-style: normal;
	font-size: 28px;
	font-family: Arial, Baskerville, monospace;
	&:hover,
	&:focus {
		opacity: 1;
	}
	&:active {
		top: 1px;
	}
}

.mfp-close-btn-in .mfp-close {
	color: #333;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
	color: #FFF;
	right: -6px;
	text-align: right;
	padding-right: 6px;
	width: 100%;
}

.mfp-counter {
	position: absolute;
	top: 0;
	right: 0;
	color: #CCC;
	font-size: 12px;
	line-height: 18px;
	white-space: nowrap;
}

.mfp-arrow {
	position: absolute;
	opacity: 0.65;
	margin: 0;
	top: 50%;
	margin-top: -55px;
	padding: 0;
	width: 90px;
	height: 110px;
	-webkit-tap-highlight-color: transparent;
	&:active {
		margin-top: -54px;
	}
	&:hover,
	&:focus {
		opacity: 1;
	}
	&:before {
		content: '';
		display: block;
		width: 0;
		height: 0;
		position: absolute;
		left: 0;
		top: 0;
		margin-top: 35px;
		margin-left: 35px;
		border: medium inset transparent;
	}
	&:after {
		content: '';
		display: block;
		width: 0;
		height: 0;
		position: absolute;
		left: 0;
		top: 0;
		margin-top: 35px;
		margin-left: 35px;
		border: medium inset transparent;
		border-top-width: 13px;
		border-bottom-width: 13px;
		top: 8px;
	}
	&:before {
		border-top-width: 21px;
		border-bottom-width: 21px;
		opacity: 0.7;
	}
}

.mfp-arrow-left {
	left: 0;
	&:after {
		border-right: 17px solid #FFF;
		margin-left: 31px;
	}
	&:before {
		margin-left: 25px;
		border-right: 27px solid #3F3F3F;
	}
}

.mfp-arrow-right {
	right: 0;
	&:after {
		border-left: 17px solid #FFF;
		margin-left: 39px;
	}
	&:before {
		border-left: 27px solid #3F3F3F;
	}
}

.mfp-iframe-holder {
	padding-top: 40px;
	padding-bottom: 40px;
	.mfp-content {
		line-height: 0;
		width: 100%;
		max-width: 900px;
	}
	.mfp-close {
		top: -40px;
	}
}

.mfp-iframe-scaler {
	width: 100%;
	height: 0;
	overflow: hidden;
	padding-top: 56.25%;
	iframe {
		position: absolute;
		display: block;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
		background: #000;
	}
}


/* Main image in popup */

img.mfp-img {
	width: auto;
	max-width: 100%;
	height: auto;
	display: block;
	line-height: 0;
	box-sizing: border-box;
	padding: 40px 0 40px;
	margin: 0 auto;
}


/* The shadow behind the image */

.mfp-figure {
	line-height: 0;
	&:after {
		content: '';
		position: absolute;
		left: 0;
		top: 40px;
		bottom: 40px;
		display: block;
		right: 0;
		width: auto;
		height: auto;
		z-index: -1;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
		background: #444;
	}
	small {
		color: #BDBDBD;
		display: block;
		font-size: 12px;
		line-height: 14px;
	}
	figure {
		margin: 0;
	}
}

.mfp-bottom-bar {
	margin-top: -36px;
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	cursor: auto;
}

.mfp-title {
	text-align: left;
	line-height: 18px;
	color: #F3F3F3;
	word-wrap: break-word;
	padding-right: 36px;
}

.mfp-image-holder .mfp-content {
	max-width: 100%;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
	cursor: pointer;
}

@media screen and (max-width: 800px) and (orientation: landscape),
screen and (max-height: 300px) {
	/**
       * Remove all paddings around the image on small screen
       */
	.mfp-img-mobile {
		.mfp-image-holder {
			padding-left: 0;
			padding-right: 0;
		}
		img.mfp-img {
			padding: 0;
		}
		.mfp-figure {
			&:after {
				top: 0;
				bottom: 0;
			}
			small {
				display: inline;
				margin-left: 5px;
			}
		}
		.mfp-bottom-bar {
			background: rgba(0, 0, 0, 0.6);
			bottom: 0;
			margin: 0;
			top: auto;
			padding: 3px 5px;
			position: fixed;
			box-sizing: border-box;
			&:empty {
				padding: 0;
			}
		}
		.mfp-counter {
			right: 5px;
			top: 3px;
		}
		.mfp-close {
			top: 0;
			right: 0;
			width: 35px;
			height: 35px;
			line-height: 35px;
			background: rgba(0, 0, 0, 0.6);
			position: fixed;
			text-align: center;
			padding: 0;
		}
	}
}

@media all and (max-width: 900px) {
	.mfp-arrow {
		-webkit-transform: scale(0.75);
		transform: scale(0.75);
	}
	.mfp-arrow-left {
		-webkit-transform-origin: 0;
		transform-origin: 0;
	}
	.mfp-arrow-right {
		-webkit-transform-origin: 100%;
		transform-origin: 100%;
	}
	.mfp-container {
		padding-left: 6px;
		padding-right: 6px;
	}
}